import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DemoComponent } from './demo/demo.component';
import { ModelsComponent } from './models/models.component';

import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { AnimationComponent } from './animation/animation.component';

import { FineArtComponent } from './fine-art/fine-art.component';
import { PlaceToBeComponent } from './place-to-be/place-to-be.component';
import { ResumeComponent } from './resume/resume.component';
import { RigsOtherComponent } from './rigs/rigs-other/rigs-other.component';
import { FacialComponent } from './rigs/facial/facial.component';
import { WarrentTechComponent } from './rigs/warrent-tech/warrent-tech.component';

const routes: Routes = [

  {
    path: '',
    redirectTo: 'demo',
    pathMatch: 'full'
  },
  {
    path: 'agPort',
    redirectTo: 'demo',
    pathMatch: 'full'
  },
  {
    path: 'demo',
    component: DemoComponent
  },
  {
    path: 'models',
    component: ModelsComponent
  },
  {
    path: 'rigsOther',
    component: RigsOtherComponent
  },
  {
    path: 'facial',
    component: FacialComponent
  },
  {
    path: 'about',
    component: AboutComponent
  },
  {
    path: 'warrentTech',
    component: WarrentTechComponent
  },
  {
    path: 'contact',
    component: ContactComponent
  },
  {
    path: 'animation',
    component: AnimationComponent
  },
  {
    path: 'fineart',
    component: FineArtComponent
  },
  {
    path: 'placeToBe',
    component: PlaceToBeComponent
  },
  {
    path: 'resume',
    component: ResumeComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
