import { Component, OnInit } from '@angular/core';
import { EnvironmentSettingsService } from 'src/app/service/enviroment-settings.service';

@Component({
  selector: 'app-facial',
  templateUrl: './facial.component.html',
  styleUrls: ['./facial.component.scss']
})
export class FacialComponent implements OnInit {

  imgPath = '';
  modalImg = '';
  playVideo = true;
  modelName = '';

  constructor(
    private envSettings: EnvironmentSettingsService
  ) { }


  ngOnInit() {
    // determine image path
    this.imgPath = this.envSettings.getServiceBase();

  }

  openModel(image, name) {
    this.modalImg = image;
    this.modelName = name;
    setTimeout(() => {
      document.getElementById('ticketModal').style.display = 'block';
    }, 500);

  }

}
